* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:before, :after {
  box-sizing: border-box;
}

a {
  color: inherit;
  text-decoration: none;
}

ul, ol {
  list-style: none;
}

body {
  background: #f4f4f4;
}

@media (width >= 500px) {
  .header {
    margin: 100px 0;
  }
}

.searchForm {
  justify-content: space-around;
  align-items: center;
  margin: 40px 20px;
  display: flex;
}

.searchForm > input {
  width: 100%;
  height: 40px;
  border: none;
  border-radius: 10px;
  margin-right: 20px;
  padding: 0 12px;
  font-size: 1.5em;
}

@media (width >= 500px) {
  .searchForm > input {
    transition: all .3s;
  }

  .searchForm > input:hover {
    box-shadow: 0 0 11px #21212133;
  }

  .searchForm > input:focus {
    outline: none;
    box-shadow: 0 0 11px #21212133;
  }

  .searchForm > button {
    transition: all .3s;
  }

  .searchForm > button:hover {
    box-shadow: 0 0 11px #21212133;
  }
}

.searchForm > button {
  color: #fff;
  cursor: pointer;
  background-color: #77b5fe;
  border: none;
  border-radius: 10px;
  padding: 12px;
  font-size: 1em;
}

@media (width >= 500px) {
  .searchForm {
    max-width: 500px;
    margin-left: auto;
    margin-right: auto;
  }

  .main {
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
  }
}

.siteList {
  flex-wrap: wrap;
  justify-content: space-between;
  margin: 0 25px;
  display: flex;
}

@media (width >= 500px) {
  .siteList {
    justify-content: flex-start;
    margin-left: 0;
    margin-right: -25px;
  }

  .siteList > li {
    margin-right: 25px;
  }
}

.siteList > li {
  background-color: #fff;
  border-radius: 10px;
  margin-bottom: 25px;
  transition: all .3s;
}

.siteList li .close {
  display: none;
  position: absolute;
  top: 10px;
  right: 10px;
}

.siteList li:hover {
  box-shadow: 0 0 11px #21212133;
}

.siteList li:hover .close {
  cursor: default;
  display: block;
}

